import { AppRoutingModule } from './app-routing.module';
import { OpportunityEditorComponent } from './opportunity-editor/opportunity-editor.component';
import { OpportunityService } from './services/opportunity.service';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { MyDatePickerModule } from './../../node_modules/angular4-datepicker/src/my-date-picker';
import { AngularMultiSelectModule } from './../../node_modules/angular2-multiselect-dropdown/angular2-multiselect-dropdown';
import { ModalModule } from "ng2-bootstrap";

@NgModule({
  declarations: [
    AppComponent,
    OpportunityEditorComponent
  ],
  imports: [
    BrowserModule, FormsModule,
    HttpModule,
    AppRoutingModule, MyDatePickerModule, AngularMultiSelectModule,
    ModalModule.forRoot()
  ],
  providers: [OpportunityService],
  bootstrap: [AppComponent]
})
export class AppModule { }



