import { Injectable } from '@angular/core';
import { Http, Response, RequestOptions, Headers } from '@angular/http';

import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';

@Injectable()
export class OpportunityService {
  private baseUrl: string = 'http://gisapi-web-staging-1636833739.eu-west-1.elb.amazonaws.com/v2';
  private accessToken: string = '?access_token=dd0df21c8af5d929dff19f74506c4a8153d7acd34306b9761fd4a57cfa1d483c';
  constructor(private http: Http) { }

  fetchOppDetails(opportunityId) {
    let data = this.http
      .get(this.baseUrl + '/opportunities/' + opportunityId + this.accessToken, { headers: this.getHeaders() })
      .map(res => res.json())
    return data;
  }

  fetchBackgroundDetails() {
    let data = this.http
      .get(this.baseUrl + '/lists/backgrounds/' + this.accessToken, { headers: this.getHeaders() })
      .map(res => res.json())
    return data;
  }
  
  fetchSkillDetails() {
    let data = this.http
      .get(this.baseUrl + '/lists/skills' + this.accessToken +'&with_parents=false', { headers: this.getHeaders() })
      .map(res => res.json())
    return data;
  }

  saveOppDetails(opportunityId, oppTitle, description) {
    var obj = {};
    let body =  JSON.stringify(obj);
    let data = this.http
      .patch(this.baseUrl + '/opportunities/' + opportunityId + this.accessToken, { headers: this.getHeaders() })
      .map(res => res.json())
    return data;
  }

  private getHeaders() {
    let headers = new Headers();
    headers = this.getAuthHeader();
    headers.append('Accept', 'application/json');
    return headers;
  }

  getAuthHeader(): Headers {
    let authorizationHeaders = new Headers();
    authorizationHeaders.append('Authorization', this.accessToken);
    return authorizationHeaders;
  }
}



