import { OpportunityService } from './../services/opportunity.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { IMyDpOptions, IMyDate } from './../../../node_modules/angular4-datepicker/src/my-date-picker';
import { ModalDirective } from 'ng2-bootstrap';

@Component({
  selector: 'app-opportunity-editor',
  templateUrl: './opportunity-editor.component.html',
  styleUrls: ['./opportunity-editor.component.css']
})
export class OpportunityEditorComponent implements OnInit {
  opportunityId: number = null;
  oppTitle: string;
  appCloseDate: Date;
  earliestStartDate: Date;
  latestEndDate: Date;
  description: string;
  backgrounds: any[] = [];
  skills: any[] = [];
  selectionProcess: string;
  salary: number;
  showContent: boolean = false;
  preferredBgFlag: boolean = false;
  reqBgFlag: boolean = false;
  preferredSkillFlag: boolean = false;
  reqSkillFlag: boolean = false;
  preferredBgLabel: string = "";
  reqBgLabel: string = "";
  preferredSkillLabel: string = "";
  reqSkillLabel: string = "";
  editMode: boolean = false;
  myDatePickerOptions: IMyDpOptions = {
    // other options...
    dateFormat: 'dd mmm yyyy',
  };
  backGroundList = [];
  prefBgList = [];
  reqBgList = [];
  skillList = [];
  reqSkillList = [];
  prefSkillList = [];

  dropdownSettings: any = {
    singleSelection: false,
    text: "Select",
    enableSearchFilter: false,
    enableCheckAll: false,
    classes: "myclass custom-class",
    maxHeight: 150
  };

  appCloseDateModel: any = null;
  earliestStartDateModel: any = null;
  latestEndDateModel: any = null;
  // selDate: IMyDate = {year: 0, month: 0, day: 0};

  @ViewChild('errorModal') public errorModal: ModalDirective;
  
  constructor(private opportunityService: OpportunityService) { }

  ngOnInit() {

  }
  opportunity: number;
  fetchOpp() {
    this.editMode = false;
    this.preferredBgLabel = "";
    this.reqBgLabel = "";
    this.preferredSkillLabel = "";
    this.reqSkillLabel = "";
    this.opportunityService.fetchOppDetails(this.opportunityId).subscribe(
      data => {
        this.oppTitle = data.title;
        this.appCloseDate = data.applications_close_date;
        this.earliestStartDate = data.earliest_start_date;
        this.latestEndDate = data.latest_end_date;
        this.description = data.description;
        this.backgrounds = data.backgrounds;
        this.skills = data.skills;
        this.selectionProcess = data.role_info.selection_process;
        this.salary = data.specifics_info.salary;

        this.showContent = true;

        for (let item of this.backgrounds) {
          if (item.option == 'preferred') {
            this.preferredBgFlag = true;
            if (this.preferredBgLabel == "") {
              this.preferredBgLabel = item.name;
            }
            else {
              this.preferredBgLabel = this.preferredBgLabel + ", " + item.name;
            }
          }
          else {
            this.reqBgFlag = true;
            if (this.reqBgLabel == "") {
              this.reqBgLabel = item.name;
            }
            else {
              this.reqBgLabel = this.reqBgLabel + ", " + item.name;
            }
          }
        }

        for (let item of this.skills) {
          if (item.option == 'preferred') {
            this.preferredSkillFlag = true;
            if (this.preferredSkillLabel == "") {
              this.preferredSkillLabel = item.name;
            }
            else {
              this.preferredSkillLabel = this.preferredSkillLabel + ", " + item.name;
            }
          }
          else {
            this.reqSkillFlag = true;
            if (this.reqSkillLabel == "") {
              this.reqSkillLabel = item.name;
            }
            else {
              this.reqSkillLabel = this.reqSkillLabel + ", " + item.name;
            }
          }
        }
        var d = new Date(this.appCloseDate);
        this.appCloseDateModel = { date: { year: d.getFullYear(), month: d.getMonth() + 1, day: d.getDay() + 1 } };
        d = new Date(this.earliestStartDate);
        this.earliestStartDateModel = { date: { year: d.getFullYear(), month: d.getMonth() + 1, day: d.getDay() + 1 } };
        d = new Date(this.latestEndDate);
        this.latestEndDateModel = { date: { year: d.getFullYear(), month: d.getMonth() + 1, day: d.getDay() + 1 } };



        // this.selDate = {year: this.appCloseDate.getFullYear(), 
        //   month: this.appCloseDate.getMonth() + 1, 
        //   day: this.appCloseDate.getDate()};
        // var b = this.appCloseDate.getFullYear();
        // this.model= { date: { year: this.appCloseDate.getFullYear(), 
        //   month: this.appCloseDate.getMonth(),
        //    day: this.appCloseDate.getDay() } };

      }
    )
  }

  editFunc() {
    this.editMode = true;
    this.backGroundList = [];
    this.skillList = [];
    this.prefBgList = [];
    this.reqBgList = [];
    this.prefSkillList = [];
    this.reqSkillList = [];


    for (let item of this.backgrounds) {
      if (item.option == "preferred") {
        this.prefBgList.push({ id: item.id, itemName: item.name });
      }
      else if (item.option == "required") {
        this.reqBgList.push({ id: item.id, itemName: item.name });
      }
    }
    for (let item of this.skills) {
      if (item.option == "preferred") {
        this.prefSkillList.push({ id: item.id, itemName: item.name });
      }
      else if (item.option == "required") {
        this.reqSkillList.push({ id: item.id, itemName: item.name });
      }
    }
    this.opportunityService.fetchBackgroundDetails().subscribe(
      data => {
        for (let item of data) {
          this.backGroundList.push({ id: item.id, itemName: item.name });
        }
      }
    );
    this.opportunityService.fetchSkillDetails().subscribe(
      data => {
        for (let item of data) {
          this.skillList.push({ id: item.id, itemName: item.name });
        }
      }
    );
  }



  saveFunc() {
    // this.errorModal.show();
    // this.opportunityService.saveOppDetails(this.opportunityId, this.oppTitle, this.description).subscribe(
    //   data => {

    //   }
    // );
    this.fetchOpp();

  }

  cancelFunc() {
    this.fetchOpp();
  }

}

